import React, { useRef, useEffect, useState } from "react";
import { Header } from "../components/Header";
import { FooterLogin } from "../components/FooterLogin";
import axios from "axios";
import { useNavigate } from "react-router-dom";
import SuccessPop from "../components/SuccessPop";
import ErrorPop from "../components/ErrorPop";
import { useTranslation } from "react-i18next";
import "../i18n";
function ForgotPassword() {
  const [err_email, seterr_email] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [question, setquestion] = useState("");
  const [securityAnswer, setSecurityAnswer] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [showPopSuccess, setShowPopSuccess] = useState(false);
  const [showPopError, setShowPopError] = useState(false);
  const [successPopMessage, setPopsuccessMessage] = useState("");
  const [errorPopMessage, setPoperrorMessage] = useState("");
  const [IpAddress, setIpAddress] = useState("");
  const [LocationData, setLocationData] = useState([]);
  const [showerror, setshowerror] = useState(false);
  const [showsuccess, setshowsuccess] = useState(false);
  const [errormessage, seterrormessage] = useState("");
  const successRef = useRef(null);
  const errorRef = useRef(null);
  const [errormessages, seterrormessages] = useState("");
  const navigate = useNavigate();
  const { t, i18n } = useTranslation();
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);
    if (userLogin != null) {
      navigate("/dashboard");
    }
  }, []);
  useEffect(() => {
    // Fetch IP address from ipify API
    const getIpAddress = async () => {
      try {
        const response = await axios.get("https://api.ipify.org?format=json");

        setIpAddress(response.data.ip); // Update state with IP address
      } catch (error) {
        console.error("Error fetching IP address:", error);
      }
    };

    getIpAddress();
  }, []);

  const getLocationDetails = async () => {
    try {
      // Step 1: Get the user's public IP address
      const ipResponse = await axios.get("https://api.ipify.org?format=json");
      const ip = ipResponse.data.ip;

      // Step 2: Use ipapi to get latitude and longitude based on IP address
      const geoResponse = await axios.get(`https://ipapi.co/${ip}/json/`);
      console.log(geoResponse.data);
      setLocationData(geoResponse.data);

      // Set the location details
    } catch (error) {
      console.error("Error fetching location data:", error);
    }
  };

  useEffect(() => {
    getLocationDetails(); // Fetch location details on component mount
  }, []);
  useEffect(() => {
    const savedEmail = localStorage.getItem("email");
    const savedPassword = localStorage.getItem("password");

    if (savedEmail) {
      setEmail(savedEmail);
      setPassword(savedPassword || ""); // Set password if available
      setRememberMe(true); // Assume the user wants to be remembered
    }
  }, []);
  var apiURL = "https://backend.amourette.no/api/";
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (question && !securityAnswer) {
      seterrormessages(
        "Please provide an answer to your selected security question."
      );
      return;
    } else {
      seterrormessages("");
    }
    let userData = {
      email: email,
      question: question,
      securityAnswer: securityAnswer,
    };
    try {
      const res = await axios.post(apiURL + "forgotPassword", userData, {
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
      });
      var check = res.data;
      seterrormessage(check.message);
      if (check.status === "2") {
        setshowerror(true);
        setshowsuccess(false);
      }
      if (check.status === "1") {
        setshowsuccess(true);
        setshowerror(false);
        setTimeout(() => {
          window.location.reload(); // Make sure to call reload(), not just reference it
        }, 2500);
      }
      setTimeout(() => {
        setshowerror(false);
        setshowsuccess(false);
      }, 2000);
    } catch (err) {}
  };
  return (
    <>
      <Header />
      {showsuccess && <SuccessPop message={errormessage} />}
      {showerror && <ErrorPop message={errormessage} />}
      <section className="regblock login_registermain d-block">
        <div className="container-lg">
          <div className="row justify-content-start">
            <div className="col-md-6 px-0">
              <div className="regform d-flex flex-column gap-4">
                <form onSubmit={handleSubmit} method="post">
                  {/* 1st step */}
                  <div className="stepbox">
                    <div className="d-flex flex-column gap-3 regtitle mb-5">
                      <div className="d-flex justify-content-between">
                        <h1>{t("ForgotPassword")}</h1>
                      </div>
                      <p>{t("frogotmain")}</p>
                    </div>
                    <div className="row gy-4">
                      <div className="col-12">
                        <div className="d-flex flex-column gap-2">
                          <label htmlFor="">{t("E-mail")}</label>
                          <input
                            type="email"
                            placeholder={t("youE-mail")}
                            name="email"
                            required
                            onChange={(e) => setEmail(e.target.value)}
                          />
                        </div>
                      </div>
                      <div className="col-12">
                        <div className="d-flex flex-column gap-2">
                          <label htmlFor="first_pet">
                            {t("Chooseasecurityquestion")}?
                          </label>
                          <select
                            value={question}
                            name="security_question"
                            onChange={(e) => setquestion(e.target.value)}
                          >
                            <option value=""> {t("Selectaquestion")}</option>

                            <option value="first_pet">{t("Whatpet")}?</option>
                            <option value="mothers_maiden_name">
                              {t("Whatmaidenname")}?
                            </option>
                            <option value="middle_school">
                              {t("Whatschoolschool")}?
                            </option>
                            <option value="first_teacher">
                              {t("Whatteacher")}?
                            </option>
                          </select>
                        </div>
                        <span className="text-danger">{err_email}</span>
                      </div>
                      <div className="col-12">
                        <div className="d-flex flex-column gap-2">
                          <label htmlFor="">{t("Securityanswer")}?</label>
                          <input
                            type="text"
                            id="security-answer"
                            name="answer"
                            onChange={(e) => setSecurityAnswer(e.target.value)}
                            placeholder={t("Enteryoursecurityquestionanswer")}
                          />
                        </div>
                        <span className="text-danger">{errormessages}</span>
                      </div>

                      <div className="col-12">
                        <button type="submit" className="regbtnsubmit">
                          {t("ResetPassword")}
                        </button>
                      </div>
                    </div>
                  </div>
                </form>
              </div>
            </div>
          </div>
        </div>
      </section>
      <FooterLogin />
    </>
  );
}

export default ForgotPassword;
