import React, { useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import logo from "../assets/images/logo.png";
import { useNavigate } from "react-router-dom";
import ColorToggle from "../components/ColorToggle";
import { useTranslation } from "react-i18next";
import i18n from "../i18n"; // Make sure i18n is imported properly
export const Header = () => {
  const navigate = useNavigate();
  const location = useLocation();
  useEffect(() => {
    // Check if the username key exists in session storage
    const storedUsername = localStorage.getItem("userLogin");
    const userLogin = JSON.parse(storedUsername);
    console.log(userLogin);

    if (userLogin !== null) {
      navigate("/dashboard");
    }
  }, []);

  const changeLanguage = (event) => {
    i18n.changeLanguage(event);
    localStorage.setItem("language", event);
  };
  const { t } = useTranslation();

  useEffect(() => {
    const savedLanguage = localStorage.getItem("language") || "en"; // Default to 'en' if no language is saved
    if (i18n.language !== savedLanguage) {
      i18n.changeLanguage(savedLanguage);
    }
  }, [i18n, t]);

  return (
    <header className="header login_header">
      <div className="container-lg">
        <div className="row gy-3 align-items-center">
          <div className="col-md-6">
            <Link to="/" className="toplogo d-inline-block fullw">
              <img
                src={logo}
                alt="Logo"
                className="object-fit-contain"
                width="200"
                height="32"
              />
            </Link>
          </div>
          <div className="col-md-6">
            <div className="d-flex justify-content-start justify-content-md-end">
              <div className="d-flex gap-3 flex-md-nowrap flex-wrap align-items-center">
                <Link
                  to="/login"
                  className={`topbtn ${
                    location.pathname === "/login"
                      ? "loginbtn active"
                      : "loginbtn"
                  }`}
                >
                  Log in
                </Link>
                <Link
                  to="/register"
                  className={`topbtn ${
                    location.pathname === "/register"
                      ? "registerbtn active"
                      : "registerbtn"
                  }`}
                >
                  Register
                </Link>
                <div className="d-flex align-items-center gap-1 lang_chnage">
                  <button
                    className={`flex-shrink-0 ${
                      i18n.language === "en" ? "active" : ""
                    }`}
                    type="button"
                    onClick={() => changeLanguage("en")}
                  >
                    EN
                  </button>
                  <h6 className="flex-shrink-0">-</h6>
                  <button
                    className={`flex-shrink-0 ${
                      i18n.language === "no" ? "active" : ""
                    }`}
                    type="button"
                    onClick={() => changeLanguage("no")}
                  >
                    NO
                  </button>
                  {/* <select
                        onClick={changeLanguage}
                        defaultValue={i18n.language}
                      >
                        <option value="en">EN</option>
                        <option value="no">NO</option>
                      </select> */}
                </div>
                <ColorToggle />
              </div>
            </div>
          </div>
        </div>
      </div>
    </header>
  );
};
